import styled from "styled-components";
import { Divider, Card } from "semantic-ui-react";

import Masonry from "react-masonry-component";

const MessageItemContainer = styled.div`
  width: 290px;
  .message_card {
    position: relative !important;
    margin-top: 15px !important;
  }
  .message_card_icon {
    position: absolute !important;
    z-index: 100;
    top: -14px;
    width: 40px;
    right: -11px;
    filter: drop-shadow(2px 2px 1px darkgray);
  }
  img {
    object-fit: cover;
    flex-shrink: 0;
  }
`;

const modSeq = (a, b) => {
  return (a % b) + 1;
};

const MessageItems = ({ data }) => {
  return (
    <Masonry
      options={{
        gutter: 20,
        columnWidth: 290,
        fitWidth: true,
        transitionDuration: 0,
      }}
      style={{ margin: "0 auto" }}
    >
      {data.map(({ message, user, image }, i) => (
        <MessageItemContainer key={i}>
          <Card className={"message_card"} centered>
            <img
              alt="icon"
              className={"message_card_icon"}
              src={`/icon/pp_${modSeq(i, 10)}.png`}
            />
            {image && <img src={image} alt={"akt"} />}
            <Card.Content>
              <Card.Description>{message}</Card.Description>
              <Divider />
              <Card.Header textAlign="right">{user}</Card.Header>
            </Card.Content>
          </Card>
        </MessageItemContainer>
      ))}
    </Masonry>
  );
};

export default MessageItems;
